
.appContainer {
    position: absolute;
    width: 100%;
    max-width: 480px;
    /* overflow: hidden; */
}

.headerContainer {
    position: relative;
    width: inherit;
    max-width: 1920px;
    height: 600px;
    overflow: hidden;
}

#p5Animation {
    display: none;
}

.glassOverlay {
    position: absolute;
    width: 1200px;
    height: 1200px;
    background-color: white;
    filter: initial;
    z-index: 1;
}

.filterMask {
    position: absolute;
    width: 100%;
    height: inherit;
    left: 50%;
    transform: translateX(-50%);
    background-color: rgb(255, 0, 0);
    opacity: 0.35;
    z-index: 1;
    animation: hueRotateAnimation ease-in-out 5s infinite alternate;
}

.imageScrollContainer {
    position: relative;
    width: 1200px;
    height: 1200px;
    /* transform: rotate(45deg); */


    .imageScrollView {
        width: 3840px;
        height: 2195px;
        background: url('../assets/images/blue_waves_mobile.png');
        background-repeat: no-repeat;
        background-position: 0px 0px;
        background-size: 75%;
        overflow: auto;
        animation: parallaxScroll ease-in-out 20s infinite alternate;
    }
}

@keyframes hueRotateAnimation {
    0% {
      filter: hue-rotate(180deg);
    }
    100% {
      filter: hue-rotate(270deg);
    }
  }

@keyframes parallaxScroll {
    from {
        transform: translateX(0px);
    }
    to {
        transform: translate(-800px, -800px);
    }
}



.devTitle {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    width: 210px;
    height: 100px;
    background-color: black;
    border-radius: 3px;
    z-index: 2;

    > h2 {
        color: white;
        height: 1rem;
        font-weight: bold;
        line-height: 0.5rem;
    }

    > h2:nth-child(2) {
        font-weight: 400;
    }
}


.projects {
    /* padding-left: 10px;
    padding-right: 10px; */
    position: relative;
    width: calc(100% - 20px);
    left: 50%;
    transform: translateX(-50%);
    h2 {
        padding-left: 10px;
    }
}

.reactPlayer {
    position: relative;
    z-index: -1;

    > video {
        height: 300px;
        max-height: 270px;
        width: 100%;
        object-fit: cover;
        /* opacity: 0.7; */
    }


}

.articleSection {
    position: relative;
    display: flex;
    width: 100%;
    left: 50%;
    transform: translateX(-50%);
    height: inherit;
    border: 1px solid #32859e;
    border-radius: 3px;
    flex-direction: column;
    margin-bottom: 40px;
    /* max-width: 350px; */
    overflow: hidden;

    > h3 {
        align-self: center;
        font-size: 1.4rem;
    }


}

.cardBackgroundUnderlay {
    position: absolute;
    width: 100%;
    height: inherit;
    border-radius: inherit;
    background-color: black;
    z-index: -1;
    opacity: 0.5;
}

.articleContainer {
    position: relative;
    display: flex;
    flex-direction: column;
    width: calc(100% - var(--offset-padding));
    left: 50%;
    transform: translateX(-50%);
}

.projectDetails {
    padding-left: 20px;
    padding-right: 20px;
}

.skillSection {
    position: relative;
    width: calc(100% - var(--offset-padding));
    height: auto;
    left:50%;
    transform: translateX(-50%);
    margin-bottom: 40px;

    h2 {
        padding-left: 10px;
    }
}

.skillItemList {
    padding: 10px 10px 10px 10px;
    border: 1px solid #32859e;
    border-radius: 3px;
}

.skillItemContainer {
    position: relative;
    display: flex;
    align-items: center;
    width: inherit;
    height: 60px;
    
    &:nth-child(n + 2) {
        margin-top: 20px;
    }

    .skillDetails {
        margin-left: 20px;
        line-height: 10px;
    }
}

