    display: flex;
    flex-direction: column;
    justify-content: space-around;
    text-align: center;
    width: inherit;
    height: 100px;
    top: 100%;
    transform: translateY(-100%);
    background-color: rgba(0, 0, 0, 0.75);
    
    h2 {
        position: relative;
        top: -10px;
        height: 1rem;
    }
