.contactModule {
    position: relative;
    width: calc(100% - var(--offset-padding));
    height: 500px;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 3px;
    margin-bottom: 40px;
    background-size: auto;
    background-repeat: no-repeat;
    z-index: 100;
    background-color: lightblue;
    border-radius: 5px;
    border: 1px solid rgb(106, 106, 106);
    overflow: hidden;

    .contactDetails {
        position: absolute;
        width: calc(100% - 40px);
        height: 150px;
        display: flex;
        flex-direction: column;
        left: 50%;
        top: 40%;
        transform: translate(-50%,-40%);
        z-index: 100;
        > h2 {
            margin: 0;
            font-size: 2em;
            text-align: center;
        }
        > h2 + p {
            text-align: center;
            font-size: 18px;
        }
    
        #businessEmail {
            text-align: center;
            font-size: 24px;
          }
    }

}

