}

#ship1 {
    width: 150px;
    height: 75px;
    top: calc(100% -  5px); 
    left: -100px;
    animation: flyAngleRight 7s ease-in-out 1s infinite forwards;
}

#ship2 {
    width: 112.5px;
    height: 56.25px;
    top: calc(100% -  125px); 
    left: -100px;
    animation: flyAngleRight 7s ease-in-out 2s infinite forwards;
}

#ship3 {
    width: 75px;
    height: 37.5px;
    top: calc(100% -  225px); 
    left: -100px;
    animation: flyAngleRight 7s ease-in-out 3s infinite forwards;
}

#ship4 {
    width: 37.5px;
    height: 18.75px;
    top: calc(100% +  150px); 
    left: -100px;
    animation: flyAngleRight 7s ease-in-out 2.5s infinite forwards;
}

.ringContainer {
    position: relative;
    width: 300px;
    height: 300px;
    top: 40px;
    left: -5px;
    transform: rotate(10deg)  skew(20deg) ;
  }
  
  .ring {
    width: 100%;
    height: 100%;
  }

  .ringCircle {
    stroke: rgb(89, 191, 254);
    stroke-width: 5;
    fill: none;
    transform-origin: 50% 50%;
  }

  .ringCircle2 {
    stroke: rgb(254, 105, 89);
    stroke-width: 5;
    fill: none;
  }

  .ringCircle3 {
    stroke: rgb(89, 254, 160);
    stroke-width: 5;
    fill: none;
  }

  #ring1 {
    transform-origin: 50% 50%;
    animation: scale1 3s cubic-bezier(0.68, -0.55, 0.27, 1.55) infinite,
    fadeOut 3s linear infinite;
  }

  #ring2 {
    transform-origin: 50% 50%;
    animation: scale2 3s cubic-bezier(0.68, -0.55, 0.27, 1.55) infinite,
    fadeOut 3s linear 1s infinite;
  }

  #ring3 {
    transform-origin: 50% 50%;
    animation: scale3 3s cubic-bezier(0.68, -0.55, 0.27, 1.55) infinite,
    fadeOut 3s linear 2s infinite;
  }

@keyframes flyAngleRight {
    0% {
        transform: translate(-150px, 86.61px);
    }

    100% {
        transform: translate(1200px, -692.88px); /* Double the end point */
    }
}

































@media (min-width: 480px) {
