.night {
  position: relative;
  width: 100%;
  height: 100%;
  transform: rotateZ(150deg);
  // animation: sky 200000ms linear infinite;
}

.shootingStar {
    position: absolute;
    left: 50%;
    top: 50%;
    /* // width: 100px; */
    height: 2px;
    background: linear-gradient(-45deg, rgba(95, 145, 255, 1), rgba(0, 0, 255, 0));
    border-radius: 50%;
    filter: drop-shadow(0 0 6px rgba(105, 155, 255, 1));
    animation:
      tail var(--shooting-time) ease-in-out infinite,
      shooting var(--shooting-time) ease-in-out infinite;
    
    &::before {
      content: '';
      position: absolute;
      top: calc(50% - 1px);
      right: 0;
      /* // width: 30px; */
      height: 2px;
      background: linear-gradient(-45deg, rgba(0, 0, 255, 0), rgba(95, 145, 255, 1), rgba(0, 0, 255, 0));
      transform: translateX(50%) rotateZ(45deg);
      border-radius: 100%;
      animation: shining var(--shooting-time) ease-in-out infinite;
    }
  
    &::after {     
      content: '';
      position: absolute;
      top: calc(50% - 1px);
      right: 0;
      /* // width: 30px; */
      height: 2px;
      background: linear-gradient(-45deg, rgba(0, 0, 255, 0), rgba(95, 145, 255, 1), rgba(0, 0, 255, 0));
      transform: translateX(50%) rotateZ(45deg);
      border-radius: 100%;
      animation: shining var(--shooting-time) ease-in-out infinite;
      transform: translateX(50%) rotateZ(-45deg);
    }
    
    @for $i from 1 through 10 {
      &:nth-child(#{$i}) {
        $delay: random(9999) + 200ms;
        top: calc(50% - #{random(1000) - 500px});
        left: calc(50% - #{random(600) + 0px});
        animation-delay: $delay;
        // opacity: random(50) / 100 + 0.5;
        
        &::before,
        &::after {
          animation-delay: $delay;
        }
      }
    }
  }
  
  @keyframes tail {
    0% {
      width: 0;
    }
    
    30% {
      width: 150px;
    }
    
    100% {
      width: 0;
    }
  }
  
  @keyframes shining {
    0% {
      width: 0;
    }
    
    50% {
      width: 30px;
    }
    
    100% {
      width: 0;
    }
  }
  
  @keyframes shooting {
    0% {
      transform: translateX(0);
    }
    
    100% {
      transform: translateX(500px);
    }
  }
  
  @keyframes sky {
    0% {
      transform: rotate(45deg);
    }
    
    100% {
      transform: rotate(45 + 360deg);
    }
  }